body {
  margin: 0;
}

ol, ul {
  padding-left: 30px;
} 

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 4px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

/*
 * --- spicing up components ---
 */
/* square: 
 - bigger size and font
 - nice background color & transition
 - pointer cursor
*/
.square {
  font-size: 2rem;
  line-height: 34px;
  height: 45px;
  width: 45px;
  color: #cfc8b8;
  border: 2px solid #cfc8b8;
}
.square:hover {
  -webkit-transition: background-color 0.5s ease;
  transition: background-color 0.5s ease;
  background-color: #e6e2da;
  cursor: pointer;
}
.square.x {
  background-color: #524a4c;
}
.square.o {
  background-color: #6b948c;
}
.square.winner{
  background-color: #bd081c;
}
.board {
  position: relative;
  border: 15px solid #cfc8b8;
  box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.75);
  z-index: 10;
  width: 911px;
}

/* Game: 
 - center stage it
*/
.game {
  display: flex;
  flex-direction: row;
          justify-content: center;
  width: 100%;
  min-width: 1330px;
}

.game-board {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0.7 1;
}
.game-board:after {
  position: absolute;
  top: 0;
  left: 0;
  content: '';
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 0;
}

.game-info {
  margin: 0;
  display: flex;
  flex-direction: column;
  flex: 0.3 1;
}

.game-info__status {
  flex: 1 1;
  padding: 1rem;
}

.game-info__moves{
  flex: 4 1;
  padding: 1rem;
  max-height: 700px;
}

.game-info__status {
  font-size: 2rem;
}
.game-info__status {
  color: #fff;
  background-color: #524a4c;
}

.game-info__moves {
  background-color: #2e292a;
}

.game-info__moves .list-moves li {
  margin-bottom: 0.5rem;
}

h3 {
  text-align: center;
}

.bounce {
  background: #bd081c;
  text-align: center;
  padding: 1rem;
  padding: 1rem;
  font-size: 2rem;
  -webkit-transform: rotate(-25deg) translateX(-20px) translateY(100px);
          transform: rotate(-25deg) translateX(-20px) translateY(100px);
  -webkit-transition: 0.4s all;
  transition: 0.4s all;
}

.btn-text-big{
  font-size: 2rem;
}

.btn-custom{
  font-size: 1.2rem;
  text-align: left;
}

.text-red {
